.item {
    background-color: white;
    width: 20%;
    padding: 50px 0;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.24);
}

.itemHeader {
    display: flex;
    align-items: center;
    gap: 15px;
}

@media (max-width:750px) {
    .item {
        width: 30%;
    }
}

@media (max-width:400px) {
    .item {
        width: 85%;
    }
}